<template>
    <div class="container">
        <div class="input">
            每天交通
            <el-input v-model="price" @blur="calcMonthsDays"></el-input>
            <el-checkbox v-model="isSatForWork">是否大小周</el-checkbox>
            <el-date-picker
                v-if="isSatForWork"
                v-model="satForWorkByDate"
                @blur="check"
                type="date"
                placeholder="随便输入任意一日的上班的周六">
            </el-date-picker>
        </div>
        <el-table
            :data="calcData"
            header-cell-class-name="tableClass"
            cell-class-name="cellClass"
            :border="true"
            style="width: 100%">
            <el-table-column
                label="月份"
                prop="month"
                width="90">
            </el-table-column>
            <el-table-column
                v-for="(c,index) in contentTextArr"
                :key="index"
                :prop="c.value"
                :label="c.name"
                width="90">
            </el-table-column>

        </el-table>

    </div>
</template>

<script>

let currentYear = 2023;
export default {

    data() {
        return {
            calcData: [],
            monthsDays: [],
            contentTextArr: [{
                name: "天数",
                value: "days"
            }, {
                name: "工作日",
                value: "workDays"
            }, {
                name: "开始打折日",
                value: "discountDateStart"
            }, {
                name: "交通费",
                value: "totalPrice"
            }],
            workDays: [],
            price: null,//每天乘坐多少钱
            discountDatesStart: [],//开始打折日
            discountPriceStart: 80,//开始打折的金额
            discount: 0.8,
            isSatForWork: false,
            satForWorkByDate: ""
        }
    },
    mounted() {
        this.initData();

    },
    methods: {
        initData() {
            this.initMonth();
            this.calcMonthsDays();
        },
        /**
         * @param callback 每隔一段时间处理的事情
         * @param stopFlag
         * @param d 间隔时间 毫秒
         * @returns {Promise<void>}
         */
        async interval(callback, stopFlag, d) {
            /**
             * 睡眠
             */
            const sleep = (d = 1000) => {
                let prom = new Promise(resolve => {
                    setTimeout(() => {
                        resolve();
                    }, d);
                });
                return prom;
            }
            while (typeof stopFlag === "boolean" ? stopFlag : stopFlag()) {
                await sleep(d);
                callback();
            }
        },
        pushList() {
            this.list.push(new Date());
        },
        stopInterval() {
            this.v++;
            return this.v <= this.count;
        },
        setClock() {
            let date = new Date();
            let h = date.getHours();
            let m = date.getMinutes();
            let s = date.getSeconds();
            h = h.toString().length === 1 ? "0" + h : h;
            m = m.toString().length === 1 ? "0" + m : m;
            s = s.toString().length === 1 ? "0" + s : s;
            this.time = `${h}:${m}:${s}`;
        },
        initMonth() {
            let list = this.calcData;
            for (let i = 0; i < 12; i++) {
                let dto = {month: i + 1, days: 0, workDays: 0, discountDateStart: null, totalPrice: 0}
                this.$set(list, i, dto);
            }
        },
        /**
         * 计算指定年份每个月有多少个工作日（忽略国家规定节假日）
         * @param year 年份
         */
        calcMonthsDays() {
            let date = new Date();
            date.setFullYear(currentYear);//设置年份
            for (let i = 0; i < 12; i++) {
                date.setMonth(i);//设置月份
                let days = new Date(date.getFullYear(), date.getMonth() + 1, 0).getDate();
                //统计当前月份天数、工作日、开始打折日
                this.calcData[i].days = days;
                let dto = this.calcWorkDaysAndDiscountDateStart(date, days);
                this.calcData[i].workDays = dto.countWorkDays;
                if (dto.discountDateStart) {
                    this.calcData[i].discountDateStart = dto.discountDateStart;
                    this.calcData[i].totalPrice = dto.totalPrice;
                }
            }
        },

        /**
         * 根据日期和当月的天数，计算工作日
         * @param date 日期
         * @param days 当月的天数
         * @returns 工作日
         */
        calcWorkDaysAndDiscountDateStart(date, days) {
            let countWorkDays = 0;
            let discountDateStart = null;
            let totalPrice = 0;
            let overPriceDay = null;
            for (let i = 0; i < days; i++) {
                // 获取周几
                let currentDate = new Date(date.getFullYear(), date.getMonth(), i + 1);
                let week = currentDate.getDay();
                // 非周日、非周六
                if (week != 0 && week != 6) {
                    countWorkDays++;
                    if ((countWorkDays * this.price > this.discountPriceStart) && !discountDateStart) {
                        // 工作日*每天交通费 大于设定的金额
                        discountDateStart = currentDate.getDate();
                        // 超过设定金额的日期
                        overPriceDay = countWorkDays;
                    }
                }
            }
            totalPrice = this.discountPriceStart + ((countWorkDays - overPriceDay) * this.price * this.discount);
            return {countWorkDays, discountDateStart, totalPrice};
        },
        check() {
            const date = new Date(this.satForWorkByDate);
            if (date.getDay() !== 6) {
                this.$message.error('选择的日期非周六');
                this.satForWorkByDate = "";
            }
        },
        handleSatDateList(){

        }
    },
}
</script>
